























import Vue from 'vue';

import { Todo } from '@/models/Todo';

export default Vue.extend({
  name: 'Todo',

  props: {
    value: Object as () => Todo
  },
 
  methods: {
    toggle() {
      this.$store.dispatch('updateTodo', { ...this.value, done: !this.value.done });
    }
  }
});
